<template>
  <div>
    <div>此数据来源于链家和安居客</div>
    <div>当前均价{{average}}</div>
     <div id="month" style="width: 600px;height:400px;">    </div>

      <div id="day" style="width: 600px;height:400px;">    </div>

  </div>
</template>
<script>
  import axios from 'axios';
  import echarts from 'echarts';
  export default {
    data () {
      return {
        msg: 'hello world',
        average: '',
        change: '',
        month:{},
        day:{},
      }
    },
    mounted() {
        this.query().then((response)=>{
          this.initData(response);
          this.render('month');
          this.render('day');
        });
    },
    methods: {
      query() {
          return axios.get('/index');
      },
      initData(response) {
         this.average = response.data.average;
          this.change = response.data.change;
          this.month = response.data.month;
          this.day = response.data.day;
          console.log(response.data);
      },
      render(type) {
         // 基于准备好的dom，初始化echarts实例
        var myChart = echarts.init(document.getElementById(type));
        // 绘制图表
        myChart.setOption({
            title: { text: type+'统计图表' },
            tooltip: {},
            xAxis: {
                data: (type =='month'? this.month : this.day).map(e=>''+e.year+'-'+e.month+(e.day===0?'':'-'+e.day))
            },
            yAxis: {},
            series: [{
                name: '均价',
                type: 'bar',
                data: (type =='month'? this.month : this.day).map(e=>e.price)
            }]
        });
      }
    }
  }
</script>

<style type="text/css">

</style>



